export default {
  name: 'scenicDetailLeaveMessage',
  props: {
    ask: Object
  },
  computed: {
    showEmpty () {
      return !this.ask.ask_list || this.ask.ask_list.length === 0
    }
  },
  methods: {
    seeMoreLeaveMessage () {
		this.$push("/pages/ask/LeaveMessageList?scenicId=" + this.$parent.$routeParams.scenicId)
    },
    startLeaveMessage () {
		this.$emit('leave-message')
    }
  }
}
